.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
sans-serif;
background-color: #f2f2f2;
margin: 0;
display: flex;
justify-content: center;
align-items: center;
height: 100vh;
margin: 0;
}

.container {
background-color: rgb(235, 235, 149);
padding: 20px;
border-radius: 10px;
box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

fieldset {
border: 2px solid rgb(105, 255, 205);
border-radius: 5px;
padding: 10px;
}

legend {
background-color: aquamarine;
color: brown;
font-size: large;
padding: 5px 10px;
border-radius: 5px;
border: 1px solid brown;
}

tr {
padding: 10px;
}

label {
display: inline-block;
margin-bottom: 5px;
}
input.gen1 {
width: auto;
box-sizing: border-box;
}
input.gen2 {
width: auto;
box-sizing: border-box;
}
input,
select {
width: 100%;
padding: 8px;
box-sizing: border-box;
}

button {
background-color: #4caf50;
color: white;
padding: 10px 15px;
border: none;
border-radius: 4px;
cursor: pointer;
}

button:hover {
background-color: #45a049;
}
table {
width: 100%;
border-collapse: collapse;
background-color: rgb(48, 157, 239);
}

td {
border: 1px solid black;
padding: 10px;
text-align: Left;
}

.center {
width: 50%;
margin: auto;
padding: 20px;
align-items: center;
}
h3{
  background-color: #2bd6d6;
  text-align: right;
  width: max-content;
}
